import React, { useState } from 'react'
import { Form, Modal, Input, Row, Col, Button, Select, DatePicker, message,Checkbox } from 'antd'
import { useMutation, useQuery } from '@apollo/client'
import { ALL_QUARANTINEINFO } from '../../../graphql/quarantine'
import moment from 'moment'
import { UPDATE_CURRENTSTATE_BY_ID } from '../../../graphql/people'

const { Option } = Select

export default function EditCurrentState({ open, setOpen, peopleId, setRefetch, currentStateData, isHospital}) {
    // console.log(currentStateData)
    let [form] = Form.useForm()
    const [positiveState,setPositiveState] = useState("")

    const [clearConfirmed, setClearConfirmed] = useState(false)
    const [clearRecovered, setClearRecovered] = useState(false)
    const [clearDeath, setClearDeath] = useState(false)

    const [updateCurrentState, { data }] = useMutation(UPDATE_CURRENTSTATE_BY_ID, {
        onCompleted: ({ updateCurrentState }) => {
            if(updateCurrentState.success===true){
                setRefetch()
                message.success(updateCurrentState.message)
                setOpen(false)
                form.resetFields()
                setClearConfirmed(false)
                setClearRecovered(false)
                setClearDeath(false)
            }else{
                message.error(updateCurrentState.message)
            }
        },onError:(error)=>{
            message.error(error.message)
        }
    })
    const onFinish = (values) => {
        let newObj ={}

        if(isHospital===true){
            newObj = {...currentStateData}
        }else{
            newObj = {...currentStateData,personalInfoId: peopleId}
        }

        switch(values.currentState){
            case '':
                newObj = {...newObj,confirm: true, confirmedAt:moment(values.date), covidVariant:values.covidVariant,confirmFormFilled: moment(values.formFilled)}
                break;
            case '':
                newObj = {...newObj,recovered: true, recoveredAt: moment(values.date), recoveredFormFilled:moment(values.formFilled)}
                break;
            case '':
                newObj = {...newObj,death: true, deathAt:moment(values.date), deathFormFilled:moment(values.formFilled)}
                break;
            default:
                newObj = {...newObj}
                break;
        }

        if(values.clearConfirmed === true){
            newObj = {...newObj,confirm: false, confirmedAt:null, covidVariant:null, confirmFormFilled:null}
        }

        if(values.clearRecovered === true){
            newObj = {...newObj,recovered: false, recoveredAt: null,recoveredFormFilled:null}
        }

        if(values.clearDeath === true){
            newObj = {...newObj,death: false, deathAt:null,deathFormFilled:null}
        }

        // console.log(newObj)

        updateCurrentState({
            variables:newObj
        })

    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    const handleClearConfirmed = (e)=>{
        setClearConfirmed(e)
        if(!e){
            
        }
    }

    const handleClearRecovered = (e)=>{
        setClearRecovered(e)
        if(!e){
            form.setFieldsValue({
                clearConfirmed:false,
            })
        }
    }

    const handleClearDeath = (e)=>{
        setClearDeath(e)
        if(!e){
            setClearConfirmed(false)
            setClearRecovered(false)
            form.setFieldsValue({
                clearConfirmed:false,
                clearRecovered:false,
            })
        }
    }

    return (
        <Modal
            title=""
            visible={open}
            onOk={() => setOpen(false)}
            onCancel={() => setOpen(false)}
            footer={null}
        >
            {/* <AddQuarantine open={openModal} setOpen={setOpenModal} quarantineId={quarantineId} setQuarantineId={callbackLocation} /> */}
            <Form
                form={form}
                name="addSubCase"
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
                fields={
                    [
                        {
                            name:"formFilled",
                            value:moment()
                        }
                    ]
                }
            >
                <Row>
                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="currentState"
                            rules={[{ required: !clearDeath, message: '!' }]}
                        >
                           <Select placeholder="" style={{ width: "100%" }} onChange={(e)=> setPositiveState(e)}>
                                {/* <Option value=""></Option> */}
                                <Option value=""></Option>
                                <Option value=""></Option>
                                <Option value=""></Option>
                            </Select>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="date"
                            rules={[{ required: !clearDeath, message: '!' }]}
                        >
                            <DatePicker placeholder="" style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={ positiveState==="" ? { span: 11, offset: 0 }:{span:24}}>
                        <Form.Item
                            name="formFilled"
                            rules={[{ required: !clearDeath, message: '!' }]}
                        >
                            <DatePicker placeholder="" style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>
                    
                    { positiveState === "" ? (
                        <Col xs={24} md={{ span: 11, offset: 2 }}>
                            <Form.Item
                                name="covidVariant"
                                rules={[{ required: !clearDeath, message: '!' }]}
                            >
                                <Select placeholder="" style={{ width: "100%" }}>
                                
                                    <Option value="COVID-19">COVID-19</Option>
                                    <Option value="DELTA">DELTA</Option>
                                    <Option value="ALPHA">ALPHA</Option>
                                    <Option value=""></Option>
                                </Select>
                            </Form.Item>
                        </Col>
                    ):null}

                        <Col xs={24} md={{ span: 8 }}>
                            <Form.Item name="clearConfirmed" valuePropName="checked">
                                <Checkbox onChange={(e) => handleClearConfirmed(e.target.checked)} disabled={!clearRecovered}>
                                    
                                </Checkbox>
                            </Form.Item>
                        </Col>

                        <Col xs={24} md={{ span: 8 }}>
                            <Form.Item name="clearRecovered" valuePropName="checked" >
                                <Checkbox onChange={(e) => handleClearRecovered(e.target.checked)} disabled={!clearDeath}>
                                    
                                </Checkbox>
                            </Form.Item>
                        </Col>

                        <Col xs={24} md={{ span: 8 }}>
                            <Form.Item name="clearDeath" valuePropName="checked">
                                <Checkbox onChange={(e) => handleClearDeath(e.target.checked)} >
                                    
                                </Checkbox>
                            </Form.Item>
                        </Col>

                        <Col xs={24}>
                            <Button
                                htmlType="submit"
                                type="primary"
                                style={{ width: "100%" }}
                        >
                                
                            </Button>
                        </Col>
                    
                </Row>
            </Form>
        </Modal>
    )
}
