import React, { useContext, useState, useEffect } from 'react'
import { Modal, Form, Input, Row, Col, Button, Select, Divider, DatePicker, message, Checkbox,List,Typography, Popconfirm, Tooltip } from 'antd'
import { provinceData, districtData, communeData, villageData, genderData, nationalityData } from '../../../context/headerContext'
import { ListSelect } from '../../../static/own-comp'
import {DeleteOutlined} from '@ant-design/icons'
import { convertToCommune, convertToDistrict, convertToVillage, convertPersonalToSelect } from '../../../function/fn'
import { PeopleController } from '../../../context/peopleContext'
import { useQuery, useMutation, empty } from '@apollo/client'
import { GET_ALL_CASES_NO_LIMIT } from '../../../graphql/case'
import { CREATE_NEW_PERSON } from '../../../graphql/people' 
import { setAddPeople } from '../../../function/set'
import AddCase from '../../case/modal/addCase'
import moment from 'moment'

const { Option } = Select

export default function AddPeople({ open, setOpen,setRefetch, personalInfoId, setPersonalInfoId, personalInfoData}) {
    let [form] = Form.useForm()
    const [province, setProvince] = useState("")
    const [district, setDistrict] = useState("")
    const [commune, setCommune] = useState("")
    const [caseId, setCaseId] = useState("")
    const [caseData, setCaseData] = useState({})
    const [allCases, setAllCases] = useState([])

    const [vacciationData,setVaccinationData] = useState([])

    const [patientId, setPatientId] = useState("None")

    const [openModal, setOpenModal] = useState(false)

    const [createPersonalInfo, { loading: loadingCreate, error: errorCreate }] = useMutation(CREATE_NEW_PERSON, {
        onCompleted: ({ createPersonalInfo }) => {
            if (personalInfoId === "new") {
                if(createPersonalInfo.response.success=== false){
                    setPeopleSampleFn()
                    message.error(createPersonalInfo.response.message)
                }else{                   
                    setPersonalInfoId(createPersonalInfo.personalInfo)
                    message.success("!")

                    setOpen(false)
                    form.resetFields()
                }
            } else {
                if(createPersonalInfo.response.success=== false){
                    message.error(createPersonalInfo.response.message)
                }else{                   
                    setRefetch()
                    message.success("!")

                    setOpen(false)
                    form.resetFields()
                }
                
            }
        },
        onError: (error) => {
            message.error(error.message)
        }
    })

    const onFinish = (values) => {
        // console.log('Success:', setAddPeople(values));

        console.log(values)
        if(values.patientId==="17-"){
            message.warning("!")
            return;
        }

        createPersonalInfo({variables: {
            ...values, 
            other: values.other === undefined ? null : values.other, 
            otherAffect: values.otherAffect === undefined ? null : values.otherAffect, 
            age: parseInt(values.age), 
            times: parseInt(values.times),
            village: values.village === undefined ? "" : values.village, 
            district: values.district === undefined ? "" : values.district, 
            commune: values.commune === undefined ? "" : values.commune, 
            vaccination: vacciationData
        }})

        // if (caseId === 'new') {
        //     createPersonalInfo({ variables: { ...setAddPeople(values), case: caseData.id } })
        // } else {
            
        //     createPersonalInfo({ variables: {...setAddPeople(values),englishName: values.englishName, patientId: values.patientId, relation: values.relation, illness: values.illness }})
        // }

       
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    const setToGenderFn = (e) => {
        form.setFieldsValue({
            gender: e
        });
    };

    const setToNationalityFn = (e) => {
        form.setFieldsValue({
            nationality: e
        });
    };

    const setToProviceFn = (e) => {
        form.setFieldsValue({
            province: e,
            district: null,
            commune: null,
            village: null,
        });

        setProvince(e)
        setDistrict("")
        setCommune("")
    };


    const setToDistrictFn = (e) => {
        form.setFieldsValue({
            district: e,
            commune: null,
            village: null,
        });

        setDistrict(e)
        setCommune("")
    };

    const setToCommuneFn = (e) => {
        form.setFieldsValue({
            commune: e,
            village: null,
        });

        setCommune(e)
    };

    const setToVillageFn = (e) => {
        form.setFieldsValue({
            village: e
        });
    };

    const setVaccineTypeFn = (e) => {
        form.setFieldsValue({
            vaccineType: e
        });
    };

    const setPatientCodeFn = (e) => {
        let index = personalInfoData.findIndex(ele => ele.id === e)

        if(index !== -1){
            form.setFieldsValue({
                patientCode: e,
                patientName: personalInfoData[index].lastName + " " + personalInfoData[index].firstName
            })
        } else {
            form.setFieldsValue({
                patientCode: e,
                patientName: ""
            })
        }

        setPatientId(e)
    }


    const setToCaseFn = (e) => {
        form.setFieldsValue({
            case: e
        });
        if (e === "new") {
            setOpenModal(true)
        }

        setCaseId(e)
    };

    const callbackCase = (e) => {
        if (e === "") {
            setCaseId(e)
            form.setFieldsValue({
                case: null
            })
        } else {
            setCaseData(e)
            form.setFieldsValue({
                caseName: e.caseName
            })
        }
    }

    
    const setPeopleSampleFn = () => {
        setPersonalInfoId("")
        setOpen(false)
        // console.log(personalInfoId)
    }

    const handleAddVaccination=(e)=>{
        let date = form.getFieldValue("date")
        let times =form.getFieldValue("times")
        let vaccineType=form.getFieldValue("vaccineType")
        let vacinatedAt= form.getFieldValue("vacinatedAt")

        console.log(date, times, vaccineType,vacinatedAt)

        if(date === undefined || times === undefined || vaccineType===undefined){
            message.error("!")
        }else{
            setVaccinationData([...vacciationData,{
                date: date === null || date === undefined ? null : moment(date),
                times: parseInt(times),
                vaccineType: vaccineType,
                vacinatedAt: vacinatedAt
            }])

            form.setFieldsValue({
                date:null,
                vaccineType:null,
                vacinatedAt:null
            })
            
        }
        
    }

    const handleDeleteVaccinationData=( e)=>{
        let newArr = [...vacciationData]
        newArr.splice(e,1)
        new Promise(function(resolve){
            resolve(setVaccinationData([]))
        }).then(() => {
            setVaccinationData(newArr)
        })
        // await setVaccinationData([])
        // await setVaccinationData(newArr)
    }
        
    

    return (
        <Modal
            title=""
            visible={open}
            onOk={() => setOpen(false)}
            onCancel={() => personalInfoId === "new" ? setPeopleSampleFn() : setOpen(false)}
            // onCancel={() => setOpen(false)}
            footer={null}
            
        >
            {/* <AddCase open={openModal} setOpen={setOpenModal} caseId={caseId} setCaseId={callbackCase} /> */}
            <Form
                form={form}
                name="addPeople"
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
                fields={
                    [
                        {
                        name:"times",
                        value: vacciationData.length+1,
                        
                        },
                        {
                            name:"patientId",
                            value:"17-"
                        }
                        
                    ]
                }
            >
                <Row>
                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="idCard"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="patientId"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="lastName"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="firstName"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="englishName"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="dob"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <DatePicker style={{width: "100%"}} placeholder=""/>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="gender"
                            rules={[{ required: true, message: '!' }]}
                        >

                            <ListSelect type={0} data={genderData} title="" setValue={setToGenderFn} />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="age"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" type="number" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="occupation"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="tel"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" addonBefore="+855" type="number" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="nationality"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <ListSelect type={0} data={nationalityData} title="" setValue={setToNationalityFn} />
                        </Form.Item>
                    </Col>



                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="province"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <ListSelect type={1} data={provinceData} title="/" setValue={setToProviceFn} />
                        </Form.Item>
                    </Col>

                    {province === "" ? (
                        <>
                            <Col xs={24} md={{ span: 11 }}>
                                <Form.Item
                                    name="district"
                                    rules={[{ required: true, message: '!' }]}
                                >
                                    <ListSelect type={0} data={convertToDistrict(districtData)} title="/" setValue={setToDistrictFn} disabled={province !== "" ? true : false} />
                                </Form.Item>
                            </Col>
                            <Col xs={24} md={{ span: 11, offset: 2 }}>
                                <Form.Item
                                    name="commune"
                                    rules={[{ required: true, message: '!' }]}
                                >
                                    <ListSelect type={1} data={convertToCommune(district, communeData)} title="/" setValue={setToCommuneFn} disabled={district === "" || district === null ? true : false} />
                                </Form.Item>
                            </Col>
                            <Col xs={24} md={{ span: 24 }}>
                                <Form.Item
                                    name="village"
                                    rules={[{ required: true, message: '!' }]}
                                >
                                    <ListSelect type={1} data={convertToVillage(commune, villageData)} title="" setValue={setToVillageFn} disabled={commune === "" || commune === null ? true : false} />
                                </Form.Item>
                            </Col>
                        </>
                    ) : null}

                    {/* <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="relation"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col> */}

                    {/* <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="vaccinated"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col> */}

                    <Col xs={24} md={{ span: 24, offset: 0 }}>
                        <Form.Item
                            name="chronic"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>
                    
                    <Divider></Divider>
                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="date"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <DatePicker style={{width: "100%"}} placeholder=""/>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="times"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="vaccineType"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Select placeholder="">
                                <Option value="SINOPHARM">SINOPHARM</Option>
                                <Option value="SINOVAC">SINOVAC</Option>
                                <Option value="ASTRAZENECA">ASTRAZENECA</Option>
                                <Option value="JOHNSON JOHNSON">JOHNSON JOHNSON</Option>
                                <Option value="PFIZER">PFIZER</Option>
                                <Option value="Other">Other</Option>
                            </Select>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="vacinatedAt"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset:0 }}>
                        <Form.Item>
                        <Button
                            // htmlType="submit"
                            // type="primary"
                            onClick={(e)=> handleAddVaccination(e)}
                            style={{ width: "100%" }}
                        >
                            
                        </Button>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 24, offset: 0 }}>
                        {/* <Form.Item> */}
                        <List
                            // header={null}
                            // footer={null}
                            
                            bordered
                            dataSource={vacciationData}
                            
                            renderItem={(item,index) => (
                                
                                <List.Item
                                    actions={
                                        vacciationData?.length-1 === index ?
                                        [
                                            
                                            <Tooltip className="deleteVacction" title="">         
                                                <Popconfirm
                                                    title="?"
                                                    onConfirm={() => { handleDeleteVaccinationData(index) }}
                                                    okText=""
                                                    cancelText=""
                                                >
                                                    <span style={{ color: "red" }}><DeleteOutlined /></span>
                                                </Popconfirm>
                                            </Tooltip>
                                        ]:null
                                    }        
                                    placeholder={[{empty:""}]}                        
                                >
                                <Row>
                                    <Col xs={24} md={24}>
                                        <Typography.Text mark>[{item?.vaccineType}]</Typography.Text>  {item.times}, {moment(item.date).format("DD MM YYYY")}
                                    </Col>
                                    {/* <Col xs={24} md={6}>
                                        
                                    </Col> */}
                                </Row>
                                </List.Item>
                            )}
                        />
                        {/* </Form.Item> */}
                    </Col>

                    <Divider></Divider>
                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="patientCode"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <ListSelect type={8} data={convertPersonalToSelect(personalInfoData)} title="" setValue={setPatientCodeFn} />
                        </Form.Item>
                    </Col>
                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                         <Form.Item
                             name="patientName"
                         // rules={[{ required: true, message: '!' }]}
                         >
                             <Input placeholder="" disabled={patientId === "" ? false : true} />
                         </Form.Item>
                     </Col>
                   
                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="direct"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Select placeholder="">
                                <Option value={true}></Option>
                                <Option value={false}></Option>
                            </Select>
                        </Form.Item>
                    </Col>
                        <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="relation"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col xs={24} md={{ span: 24, offset: 0 }}>
                        <Form.Item
                            name="otherAffect"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>
                    

                    {/* <Divider></Divider>
                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="case"
                            rules={[{ required: true, message: '!' }]}
                        >

                            <ListSelect type={4} data={allCases} title="" setValue={setToCaseFn} disabled={caseId === "new" ? true : false} />
                        </Form.Item>
                    </Col>

                    {
                        caseId === "new" ? (
                            <Col xs={24} md={{ span: 11, offset: 2 }}>
                                <Form.Item
                                    name="caseName"
                                >
                                    <Input disabled={true} style={{ backgroundColor: "white", color: "black" }} />
                                </Form.Item>
                            </Col>
                        ) : null
                    }

                    <Col xs={24} md={caseId === "new" ? { span: 24 } : { span: 11, offset: 2 }}>
                        <Form.Item
                            name="direct"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Select placeholder="" style={{ width: "100%" }}>
                                <Option value={true}></Option>
                                <Option value={false}></Option>

                            </Select>
                        </Form.Item>
                    </Col>

                    

                    <Col xs={24} md={{ span: 24 }}>
                        <Form.Item
                            name="other"

                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col> */}


                    {/* <Col xs={24}>
                        <Form.Item
                            name="interviewed"
                            valuePropName="checked"
                        >
                            <Checkbox></Checkbox>
                        </Form.Item>
                    </Col> */}


                    <Col xs={24}>
                        <Button
                            htmlType="submit"
                            type="primary"
                            style={{ width: "100%" }}
                        >
                            
                        </Button>
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
