import React, { useContext } from 'react'
import { Modal, Form, Input, Row, Col, Button, Select, message } from 'antd';
import { useMutation } from '@apollo/client';
import { UPDATE_ACCOUNT } from '../../../graphql/auth';
import { EyeTwoTone,EyeInvisibleOutlined } from '@ant-design/icons';

const { Option } = Select;

export default function EditAccount({ open, setOpen, data,setRefetch }) {
    //const { userDataDispatch } = useContext(UserController)

    const [updateAccount, { loading, error }] = useMutation(UPDATE_ACCOUNT,{
        onCompleted:({updateAccount})=>{
            if(updateAccount.success===true){
                setRefetch()
                message.success("")
            }else{
                message.error(updateAccount.message)
            }
            
        },
        onError:(error)=>{
            message.error(error.message)
        }
    })

    let [form] = Form.useForm()

    const onFinish = (values) => {

        if(values.password.trim() === values.cfpassword.trim()){
            updateAccount({variables:{
                userId:data.id,
                username:values.username,
                password:values.password,
            }})

            setOpen(false)
            form.resetFields()
        }else{
            message.error("!")
        }

    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    const handlePreventInputSpace=(e)=>{
        if (e.key === " ") {
            message.warning("!")
            e.preventDefault();
        }
    }
    

    return (
        <Modal
            title=""
            visible={open}
            onOk={() => setOpen(false)}
            onCancel={() => setOpen(false)}
            footer={null}
        >
            <Form
                form={form}
                name="editUser"
                fields={[
                    {
                        name: ['username'],
                        value: data.username,
                    },
                    // {
                    //     name: ['firstName'],
                    //     value: data.firstName,
                    // },
                    // {
                    //     name: ['lastName'],
                    //     value: data.lastName,
                    // },
                    // {
                    //     name: ['tel'],
                    //     value: data.tel,
                    // },
                    // {
                    //     name: ['email'],
                    //     value: data.email,
                    // },
                    

                ]}
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
            >
                <Row>
                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="username"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" onKeyPress={(e)=> handlePreventInputSpace(e)} />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="password"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input.Password placeholder="" onKeyPress={(e)=> handlePreventInputSpace(e)} style={{ width: "100%" }} iconRender={visible => (visible ? <EyeTwoTone /> : <EyeInvisibleOutlined />)} />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11}}>
                        <Form.Item
                            name="cfpassword"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input.Password placeholder="" onKeyPress={(e)=> handlePreventInputSpace(e)} style={{ width: "100%" }} iconRender={visible => (visible ? <EyeTwoTone /> : <EyeInvisibleOutlined />)} />
                        </Form.Item>
                    </Col>
                    
                    <Col xs={24}>
                        <Button
                            htmlType="submit"
                            type="primary"
                            style={{ width: "100%" }}
                        >
                            
                        </Button>
                    </Col>
                </Row>
            </Form>
        </Modal >
    )
}
