export function convertForPeopleExcel(e){
    let array = []

    e?.map((load,index) => {

        array.push({
            ...load,
            ...load?.vaccination[load?.vaccination?.length-1],
            ...load?.sampleTest[load?.sampleTest?.length-1],
            no: index+1,
            patientId: load?.patientId,
            fullName: load?.lastName+" "+load?.firstName,
            village: load?.village === "" ? "":load?.village,
            commune: load?.commune === "" ? "": load?.commune,
            district: load?.district === "" ? "" : load?.district,
            date: load?.sampleTest.length === undefined ? "":  moment.parseZone(load?.sampleTest[parseInt(load?.sampleTest?.length)-1]?.date).format("DD.MM.YYYY"),
            result:load?.sampleTest.length === undefined ? "": load?.sampleTest[parseInt(load?.sampleTest?.length)-1]?.result ? "":"",
            direct:load?.affectedFrom?.direct ? "" : "",
            times: " "+load?.sampleTest?.length,
            vaccinated: load?.vaccination[parseInt(load?.vaccination?.length) -1 ]?.times > 0 ? "" : "",
            vaccineType: load?.vaccination[parseInt(load?.vaccination?.length) -1 ]?.vaccineType,
          
        })
    })
    return array
}

export function getKhmerNumber(number){

    let numArr = number?.toString()?.split('')

    let numberKh = ['','','','','','','','','','']
    let newArr = []

    for(let i=0;i<numArr?.length;i++){
    //   if(d < 10){
    //     newArr.push(numberKh[0])
    //   }
      newArr.push(numberKh[numArr[i]])
    //   if(i===numArr.length-1){
    //     newArr.push(newArr.join(""))
    //   }
    }
    return newArr?.join("")
  }

export function getKhmerDate(date){
    let d = moment.parseZone(date).format("D").toString()
    let m = moment.parseZone(date).format("M") - 1
    let y = moment.parseZone(date).format("YYYY").toString()
  
    let numArr = d.split('')
    let yearArr = y.split("")
  
    let numberKh = ['','','','','','','','','','']
    let monthKh = ['','','','','','','','','','','','']
    let newArr = []
  
    let nd =['']
    for(let i=0;i<numArr.length;i++){
      if(d < 10){
        nd.push(numberKh[0])
      }
      nd.push(numberKh[numArr[i]])
      if(i===numArr.length-1){
        newArr.push(nd.join(""))
      }
    }
  
    newArr.push(''+monthKh[m])
  
    let ny =['']
    for(let i=0;i<yearArr.length;i++){
      // if(d < 10){
      //   s.push(numberKh[0])
      // }
      ny.push(numberKh[yearArr[i]])
      if(i===yearArr.length-1){
        newArr.push(ny.join(""))
      }
    }
  
    return newArr.join(" ")
  }
  
export const subQuarantionExport = (e, x) => {
    // console.log(e, "test")
    // console.log(x, "is")

    let array = []
    
    e.map(load => {
        let obj = {}
        load.quaranting.filter(e=>e.locationName===x.locationName).map(load1 =>{
            obj = {
                ...load1,
            }
        })
        array.push({
            ...load,
            ...obj,
            coorporate: obj.coorporate ? "" : "",
            ...load?.vaccination[load?.vaccination.length-1],
            ...load?.sampleTest[load?.sampleTest.length-1],
            vaccinated: load?.vaccination[load?.vaccination.length-1]?.times > 0 ? "" : "",
            times: " " + load?.sampleTest?.length,
            fullName: load?.lastName + " " + load?.firstName,
            date: moment.parseZone(load?.sampleTest[load?.sampleTest.length-1]?.date).format("DD.MM.YYYY"),
            result: load?.sampleTest[load?.sampleTest?.length-1]?.result ? "" : "",
            direct: obj.direct === null ? "" : "",
            district: load.district === "" ? "" : load.district,
            commune: load.commune === "" ? "" : load.commune,
            village: load.village === "" ? "" : load.village,
        })
    })

    console.log(array)
    
    return array
}

