export const convertDateToKh = (e) =>{
    let day = moment(e).format("DD")
    let month = moment(e).month()
    let year = moment(e).format("YYYY")
    let numberKh = ['','','','','','','','','','']
    let monthKh = ['','','','','','','','','','','','']
    let day1 = day.charAt(0)
    let day2 = day.charAt(1)
    let year1 = year.charAt(0)
    let year2 = year.charAt(1)
    let year3 = year.charAt(2)
    let year4 = year.charAt(3)

    let khmerDate = `${numberKh[day1]+numberKh[day2]} ${monthKh[month]} ${numberKh[year1]+numberKh[year2]+numberKh[year3]+numberKh[year4]}`

    return khmerDate
}

export const totalArray = (e) => {
    let obj = {
        confirmedCase: 0, 
        confirmedCaseToday: 0,
        death: 0,
        deathToday: 0,
        delta: 0,
        deltaToday: 0,
        recovered: 0,
        recoveredToday: 0,
        sampleTest: 0,
        sampleTestToday: 0,
        totalAffectedLocation: 0,
        totalAffectedLocationClosed: 0,
        totalAffectedLocationClosedToday: 0,
        totalAffectedLocationToday: 0,
        totalAffectedPeople: 0,
        totalAffectedPeopleToday: 0,
        totalHospital: 0
    }
    e.map(load => {
        obj={
            confirmedCase: load.positive + obj.confirmedCase , 
            confirmedCaseToday: load.positiveToday + obj.confirmedCaseToday,
            death: load.death + obj.death,
            deathToday: load.deathToday + obj.deathToday,
            delta: 0,
            deltaToday: 0,
            recovered: load.recover + obj.recovered,
            recoveredToday: load.recoverToday + obj.recoveredToday,
            sampleTest: load.sampleTest + obj.sampleTest,
            sampleTestToday: load.sampleTestToday + obj.sampleTestToday,
            totalAffectedLocation: 0,
            totalAffectedLocationClosed: 0,
            totalAffectedLocationClosedToday: 0,
            totalAffectedLocationToday: 0,
            totalAffectedPeople: 0,
            totalAffectedPeopleToday: 0,
            totalHospital: 0
        }
    })

    return obj
}