import React from 'react'
import { Modal, Form, Input, Row, Col, Button} from 'antd'

export default function AddMoreHospital({open, setOpen, addMore, setAddMore}) {
    let [form] = Form.useForm()

    const onFinish = (values) => {
        setAddMore(values)

        setOpen(false)
        form.resetFields()
      };
    
      const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
      };

    return (
        <Modal
            title=""
            visible={open}
            onCancel={() => setOpen(false)}
            footer={null}
        >
            <Form
                name="form"
                fields={[
                    {
                        name:"khmerDate",
                        value: addMore.khmerDate
                    },
                    {
                        name:"englishDate",
                        value: addMore.englishDate
                    },
                    {
                        name:"name",
                        value: addMore.name
                    }
                ]}
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
            >
                <Row>
                    <Col
                        xs={24}
                    >
                        <Form.Item
                            name="khmerDate"
                        >
                            <Input placeholder="Ex:    . " />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={24}
                    >
                        <Form.Item
                            name="englishDate"
                        >
                            <Input placeholder="Ex: ,   " />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={24}
                    >
                        <Form.Item
                            name="name"
                        >
                            <Input placeholder="Ex: " />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={24}
                    >
                        <Form.Item>
                            <Button 
                                type="primary"
                                style={{
                                    width: "100%"
                                }}
                                htmlType="submit"
                            >
                                
                            </Button>
                        </Form.Item>
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
