import { Col, Row, Typography, Table, message, Input, DatePicker, List, Button, Popconfirm,Card,Modal, Space, Select } from 'antd'
import { ListGroup } from 'react-bootstrap'
import React, { useState, useEffect,useRef } from 'react'
import { useParams } from 'react-router-dom'
import moment from 'moment'
import { subHospitalCol } from './tableColumn/subHospitalColumn'
import {
    ReconciliationOutlined,
    UsergroupAddOutlined,
    UsergroupDeleteOutlined,
    UserOutlined, 
    PlusCircleOutlined, 
    EditOutlined, 
    FileExcelOutlined,
    SearchOutlined, 
    CopyOutlined, 
    DeleteOutlined,
    PlusOutlined ,
    IdcardOutlined
} from '@ant-design/icons';
import EditHospital from './modal/editHospital'
import AddSubHospital from './modal/addSubHospital'
import EditSubHospital from './modal/editSubHospital'
import { useMutation, useQuery } from '@apollo/client'
import { GET_ALL_PERSONINFO_NO_LIMIT } from '../../graphql/people'
import { GET_PATIENT_HOSPITAL_PAGINATION,DELETE_PATIENT_TO_HOSPITAL,GET_HOSPITALINFO_BY_ID, GET_HOSPITAL_DASHBOARD, GET_REPORT_WORKER } from '../../graphql/hospital'
import ReportLaboratory from '../../component/print/reportLaboratory'
import ReportLineList from '../../component/print/reportLineList';
import ReactToPrint from 'react-to-print'
import ExportToExcel from "../../component/export/exportToExcel";
import {reportSubHospital, reportSubHospitalWorker, reportSubHospitalWorker1} from '../../asset/exportExcel/reportSiemReap'
import {convertForPeopleExcel, subHospitalExport} from '../../function/fn'
import ReportConfirm from '../print/reportConfirm'
import AddSampleTest from '../sample/modal/addSampleTest'
import EditSampleTest from '../sample/modal/editSampleTest'
import { DELETE_SAMPLETEST, GET_ALL_SAMPLE_LOCATION } from '../../graphql/sample'
import AddMoreHospital from '../print/modal/addMoreHospital'
import EditCurrentState from '../people/modal/editCurrentState'

const { RangePicker } = DatePicker
const { Title } = Typography
const {Option} = Select

export default function SubHospital() {

    let { id } = useParams();

    const [updateSubData, setUpdateSubData] = useState({})
    const [peopleData, setPeopleData] = useState([])
    const [exportData, setExportData] = useState([])
    const [openEdit, setOpenEdit] = useState(false)
    const [openAddSub, setOpenAddSub] = useState(false)
    const [openEditSub, setOpenEditSub] = useState(false)
    const [subHospitalData, setSubHospitalData] = useState([])
    const [hospitalData, setHospitalData] = useState({})
    const [workerData, setWorkerData] = useState([])
    const [workerData1, setWorkerData1] = useState([])

    const [dashboard,setDashboardData] = useState({})
    const [openEditCurrentState,setOpenEditCurrentState] = useState(false)
    const [currentStateData,setCurrentStateData] = useState({})

    const [openPrintConfirm, setOpenPrintConfirm] = useState(false)
    const printConfirmRef = useRef()
    const [printData, setPrintData] = useState({})

    // sample test
    const [openAddSampleTest, setOpenAddSampleTest] = useState(false)
    const [openEditSampleTest, setOpenEditSampleTest] = useState(false)
    const [editData,setEditData] = useState({})
    const [addData,setAddData] = useState({})

    const [page, setPage] = useState(1)
    const [limit, setLimit] = useState(10)
    const [keyword, setKeyword] = useState("")
    const [patientType,setPatientType] = useState("")
    const [testCount, setTestCount] = useState(null)
    const [nextTest,setNextTest] = useState(null)
    const [ranges, setRanges] = useState({
        startDate: null,
        endDate: null,
    })

    const componentRef3 = useRef()
    const [openLabForm,setOpenLabForm] = useState(false)
    const [openAddMore, setOpenAddMore] = useState(false)
    const [labFormData, setLabFormData] = useState({})
    const [addMore, setAddMore] = useState({
        khmerDate: "   . ",
        englishDate: ",   ",
        name: ""
    })

    const [locationForFilter, setLocationForFilter] = useState([])

    const { data:locationFilter } = useQuery(GET_ALL_SAMPLE_LOCATION,{
        onCompleted:({getSampleTestLocation})=>{

            setLocationForFilter(getSampleTestLocation)
        },
        fetchPolicy:'network-only',
        nextFetchPolicy:'cache-only'
    })

    //dashboard data
    const { data:dashboardData, refetch: refetchDashboard } = useQuery(GET_HOSPITAL_DASHBOARD, {
        variables: {
            hospitalInfo: id
        },
        onCompleted: ({ hospitalDashboard }) => {

            setDashboardData(hospitalDashboard)
        }
    })

    useEffect(() => {
        if (dashboardData) {
            setDashboardData(dashboardData?.hospitalDashboard)
        }
    }, [dashboardData])

    const { data: hospital, refetch: refetchHospital } = useQuery(GET_HOSPITALINFO_BY_ID, {
        variables: {
            id: id
        },
        onCompleted: ({ getHospitalInfoById }) => {

            setHospitalData(getHospitalInfoById)
        }
    })
    useEffect(() => {
        if (hospital) {
            setHospitalData(hospital?.getHospitalInfoById)
        }
    }, [hospital])

    // GET_REPORT_WORKER
    const { data:Worker } = useQuery(GET_REPORT_WORKER, {
        variables: {
            hospitalId: id
        },
        onCompleted: ({ reportForImmigrantWorker }) => {
            let obj = {
                "()": "()"
            }

            reportForImmigrantWorker.map(load => {
                obj=Object.assign({
                    ...obj,
                    [load._id]: load.total
                })
            })
            setWorkerData([obj])
        }
    })

    const { data: hospitalSub, refetch: refetchSub,loading:loadingSub } = useQuery(GET_PATIENT_HOSPITAL_PAGINATION, {
        variables: {
            page: page,
            limit: limit,
            keyword: keyword,
            startDate: ranges.startDate,
            endDate: ranges.endDate,
            hospitalId: id,
            patientType:patientType,
            numberOfSampleTest:testCount,
            nextTest:nextTest
        },
        onCompleted: ({ getPatientForHospitalWithPagination }) => {
            setSubHospitalData(getPatientForHospitalWithPagination)

            let item = []
            getPatientForHospitalWithPagination?.personalInfos?.map(load => {
                if(load.currentState.covidVariant === "DELTA"){
                    let obj = {
                        ...load, 
                        ...load?.hospitalizations[0],
                        qurDate_in: moment(load?.quaranting[0]?.date_in).format("DD-MM-YYYY"),
                        qurDate_out: moment(load?.quaranting[0]?.date_out).format("DD-MM-YYYY"),
                        outsite: "",
                        fullName: load?.lastName + " " + load?.firstName
                    }
                    let index = 0
                    obj.sampleTest.map(load1 => {
                        // console.log(Object.keys(load1))
                        let newObj = {
                            [Object.keys(load1)[13] + index]: load1?.resultDate,
                            [Object.keys(load1)[12] + index]: load1?.result,
                            ["laboratory" + index]: load1?.laboratory,
                            ["laboratoryDate_out"]: load1?.laboratory + moment(load1?.date_out).format("(DD-MM-YYYY)"),
                            ["followup" + index]: load1?.testType + (load1?.result ? "(+) " : "(-) ") + (moment(load1?.resultDate).format("DD-MM-YY")),
                            [Object.keys(load1)[15]]: load1?.symptom,
                            [Object.keys(load1)[18]]: load1?.testType,
                            // [Object.keys(load1)[1]]: load1.covidVariant
                        }

                        item.push({
                            ...obj, 
                            ...newObj,
                            testType: moment(obj?.date_in).format("DD-MM-YY ") + newObj?.testType + "(+)"
                        })
                    })
                }
            })

            setWorkerData1(item)
        },
        onError:(error)=>{
            message.error(error.message)
        },
        fetchPolicy: 'cache-and-network'
    })

    useEffect(() => {
        if (hospitalSub) {

            // let hospitalization = hospitalSub?.getPatientForHospitalWithPagination?.hospitalizations?.filter(e=> e.hospitalName === hospitalName  )
            // let hospitalLength = hospitalization?.hospitalizations?.length

            setSubHospitalData(hospitalSub?.getPatientForHospitalWithPagination)
        }

    }, [hospitalSub])

    useEffect(() => {
        refetchSub()
    }, [page, limit, keyword, ranges, patientType])

    const [deletePatientFromHospital, { loading }] = useMutation(DELETE_PATIENT_TO_HOSPITAL, {
        onCompleted: ({deletePatientFromHospital}) => {
            if(deletePatientFromHospital.success === true){
                refetchSub()
                message.success(deletePatientFromHospital.message)
            } else {
                message.error(deletePatientFromHospital.message)
            }

        }
    })

    const [deleteSampleTest, { loading: deleteLoading }] = useMutation(DELETE_SAMPLETEST, {
        onCompleted: ({deleteSampleTest}) => {
            if(deleteSampleTest.success === true){
                refetchSub()
                message.success(deleteSampleTest.message)
            } else {
                message.error(deleteSampleTest.message)
            }
        },
        onError:(error)=>{
            message.error(error.message)
        }
    })

    const handleDelete = (h, p) => {
        deletePatientFromHospital({
            variables: {
                personalInfoId:p,
                hospitalId:h
            }
        })
    }
    const handleDeleteSampleTest = (e) => {
        deleteSampleTest({
            variables: {
                sampleTestId: e.sampleTestId,
                personalInfoId: e.personalInfoId
            }
        })
    }

    const handleEditSubHospital = (e) => {
        setUpdateSubData(e)
        setOpenEditSub(true)
    }

    const onSelectDate = (e) => {
        if(e !== null){
            let startDate = e[0]
            let endDate =e[1]
            setRanges({
                startDate: new Date(startDate),
                endDate: new Date(endDate)
            })
        }else{
            setRanges({
                startDate: null,
                endDate: null
            })
        }
    }

    const [rpsPadding,setRPSPadding] = useState("hospital-padding-df")
    const [rpsPadding2,setRPSPadding2] = useState("hospital-padding-df2")
    const [rpsDisplayNone, setRPSDisplayNone] = useState("d-block")

    const screenResponsive=()=>{

        if(window.innerWidth<=767){
            setRPSPadding("hospital-padding-rsp")
            setRPSPadding2("hospital-padding-rsp")
            // message.info("wer")
        }else{
            setRPSPadding("hospital-padding-df")
            setRPSPadding2("hospital-padding-df2")
        }

        if(window.innerWidth<=1599){
            setRPSDisplayNone("d-none")
        }else{
            setRPSDisplayNone("d-block")
        }
        
    } 
    

    window.addEventListener('resize',screenResponsive)

    const dateHolder = ["", ""]

    const handleLabForm = (e)=>{
        setLabFormData(e)
        setOpenLabForm(true)
    }

    const handleEdit=(e)=>{
        setEditData(e)
        setOpenEditSampleTest(true)
    }

    const handleAddSampleTest=(e)=>{
        setAddData(e)
        setOpenAddSampleTest(true)
    }

    const setOpenPrintConfirmFn = (e) => {
        setOpenPrintConfirm(true)
        setPrintData(e)
    }

    const handleOpenEditCurrentState=(e)=>{
        setCurrentStateData(e)
        setOpenEditCurrentState(true)
    }

    // get no for people data
    let personalDataWithNo = []
    subHospitalData?.personalInfos?.map((record,index) =>{

        let pageAdd = subHospitalData?.paginator?.currentPage > 1 ? ((subHospitalData?.paginator?.currentPage*limit)-limit)+1: 1;

        let setNoToPeopleData = {...record,no:index+pageAdd}
        personalDataWithNo.push(setNoToPeopleData)

    })

    return (
        <>
            <AddSampleTest 
                open={openAddSampleTest} 
                setOpen={setOpenAddSampleTest} 
                // personalAllData = {getPersonalInfo}
                locationAllData = {locationForFilter}
                setRefetch={refetchSub} 
                isHospital={true}
                peopleData={addData}
            />
            <EditSampleTest 
                open={openEditSampleTest} 
                setOpen={setOpenEditSampleTest} 
                data={editData}
                // personalAllData = {getPersonalInfo}
                // locationAllData = {getLocation}
                locationAllData = {locationForFilter}
                setRefetch={refetchSub} 
            />
            <Row>
                <EditHospital open={openEdit} setOpen={setOpenEdit} data={hospitalData} hospitalId={id} setRefetch={refetchHospital} />
                <AddSubHospital open={openAddSub} setOpen={setOpenAddSub} hospitalData={hospitalData} setRefetch={refetchSub} />
                <EditSubHospital open={openEditSub} setOpen={setOpenEditSub} data={updateSubData} hospitalId={id} setRefetch={refetchSub} />
                {/* currentStateData={getPersonalInfoById?.currentState} */}
                <EditCurrentState open={openEditCurrentState} setOpen={setOpenEditCurrentState} setRefetch={refetchSub} currentStateData={currentStateData} isHospital={true} />
                <Modal
                    width={900}
                    visible={openLabForm}
                    footer={[
                        <ReactToPrint
                            trigger={() => <Button type="primary" ></Button>}
                            content={() => componentRef3.current}
                        />
                    ]}
                    onCancel={() => setOpenLabForm(false)}
                >
                    <ReportLaboratory ref={componentRef3} labFormData={labFormData} />
                </Modal>
                <Col
                    xs={24}
                    md={24}
                >
                    {/* <Title level={5}> 
                    <EditOutlined className="link" onClick={() => setOpenEdit(true)}/>
                </Title> */}
                </Col>
                <Col
                            xs={12}
                            md={{span: 11, offset: 0}}
                    // className={rpsPadding}
                >
                    <Card 
                        // className="subHospital-card" 
                        className="tbl-subPeople" 
                        title={
                            <span style={{justifyContent:"space-between", display:"flex"}}>
                                <span>
                                    <span> <ReconciliationOutlined style={{fontSize:25,paddingRight: 10}}  /></span>
                                    <span className="title-card-custom " style={{fontSize:20}}></span>
                                </span>
                                <Button
                                    type="primary"   
                                    onClick={() => setOpenEdit(true)}
                                >
                                    <EditOutlined style={{color:"#FFF"}} />
                                </Button>
                            </span>
                        } 
                        style={{height:'100%'}}
                    >
                        {/* <span style={{fontSize:40}}>200</span>  */}
                        
                        <Row>
                            <Col xs={24}
                                md={{span: 11, offset: 0}}
                                >
                                
                                <table className="tbl-subPeople">
                                    <tbody>
                                    <tr>
                                        <td></td>
                                        <td></td>
                                        <td>{hospitalData?.hospitalName}</td>
                                    </tr>
                                    <tr>
                                        <td></td>
                                        <td>&emsp; </td>
                                        <td>
                                            {hospitalData?.village !== "" && hospitalData?.village + ","}
                                            {hospitalData?.commune !== "" && hospitalData?.commune + ","}
                                            {hospitalData?.district !== "" && hospitalData?.district + ","}
                                            {hospitalData?.province}
                                        </td>
                                        
                                    </tr>
                                    <tr>
                                        <td></td>
                                        <td>&emsp; </td>
                                        <td>{hospitalData?.locationType}</td>
                                    </tr>
                                    <tr>
                                        <td></td>
                                        <td>&emsp; </td>
                                        <td>{hospitalData?.other}</td>
                                    </tr>
                                    </tbody>
                                </table>
                            </Col>
                            
                            <Col  xs={24}
                                    md={{span: 11, offset: 2}}>
                               
                                <table className="tbl-subPeople">
                                    <tbody>
                                    <tr>
                                        <td></td>
                                        <td>&emsp;</td>
        
                                        <td>{hospitalData?.personInCharge?.lastName} {hospitalData?.personInCharge?.firstName}</td>
                                        {/* <td>&emsp;</td>
                                        <td>&emsp;</td>
                                        <td>&emsp;</td> */}
                                    </tr>
                                    <tr>
                                        <td></td>
                                        <td>&emsp;</td>
                                        <td>
                                            {hospitalData?.personInCharge?.tel}
                                        </td>
                                        
                                    </tr>
                                    <tr>
                                        <td></td>
                                        <td>&emsp; </td>
                                        <td>{hospitalData?.personInCharge?.position}</td>
                                    </tr>
                                    <tr>
                                        <td></td>
                                        <td>&emsp; </td>
                                        <td>{hospitalData?.personInCharge?.others}</td>
                                    </tr>
                                </tbody>
                                </table>
                            </Col>
                        </Row>

                    </Card>
                    
                
                </Col>
                <Col
                    xs={24}
                    md={{span:12}}
                    // className="subCase-card"
                    className={rpsPadding2}
                >
                    <Row style={{height:'70%'}}>
                        <Col xs={24}
                                md={8}>
                            <Card
                            className="tbl-subPeople" 
                             title={
                            <span> 
                                <span> <UsergroupAddOutlined style={{ fontSize: '23px',color:"red",paddingRight: 10}} /></span>
                                <span className="title-card-custom"></span>
                            </span>
                             
                             } style={{height:'100%'}}>
                                 <center>
                                        <table className="tbl-subPeople">
                                            <tbody>
                                                <tr>
                                                    <td></td>
                                                    <td> &emsp;</td>
                                                    <td>{dashboard?.totalIn} </td>
                                                </tr>
                                                <tr>
                                                    <td></td>
                                                    <td> &emsp;</td>
                                                    <td>{dashboard?.totalInWomen} </td>
                                                </tr>
                                                <tr>
                                                    <td></td>
                                                    <td>&emsp;</td>
                                                    <td>{dashboard?.totalInToday} </td>
                                                </tr>
                                                <tr>
                                                    <td><b>DELTA</b></td>
                                                    <td>&emsp;</td>
                                                    <td>{dashboard?.totalDeltaIn} </td>
                                                </tr>
                                            </tbody>
                                        </table>
                                    </center>
                            </Card>
                        </Col>
                        <Col xs={24}
                                md={8}> 
                            <Card className="tbl-subPeople"  
                            title={
                            <span>
                                <span> <UsergroupDeleteOutlined style={{ fontSize: '23px',color:"green",paddingRight: 10}} /></span>
                                <span className="title-card-custom"></span>
                            </span>
                            } style={{height:'100%'}}>
                                  <center>
                                        <table className="tbl-subPeople">
                                            <tbody>
                                                <tr>
                                                    <td></td>
                                                    <td>&emsp;</td>
                                                    <td>{dashboard?.totalOut} </td>
                                                </tr>
                                                <tr>
                                                    <td></td>
                                                    <td> &emsp;</td>
                                                    <td>{dashboard?.totalOutWomen} </td>
                                                </tr>
                                                <tr>
                                                    <td></td>
                                                    <td>&emsp;</td>
                                                    <td>{dashboard?.totalOutToday} </td>
                                                </tr>
                                                <tr>
                                                    <td><b>DELTA</b></td>
                                                    <td>&emsp;</td>
                                                    <td>{dashboard?.totalOutDelta} </td>
                                                </tr>
                                            </tbody>
                                        </table>
                                    </center>
                            </Card>
                        </Col>
                        <Col xs={24}
                                md={8} >
                            <Card className="tbl-subPeople"  title={
                            <span>
                                <span><UserOutlined style={{ fontSize: '23px',color:"red",paddingRight: 10}} /></span>
                                <span className="title-card-custom"></span>
                            </span>
                            } style={{height:'100%'}}>
                                  <center>
                                        <table className="tbl-subPeople">
                                            <tbody>
                                                <tr>
                                                    <td></td>
                                                    <td> &emsp;</td>
                                                    <td>{dashboard?.totalIn - dashboard?.totalOut} </td>
                                                </tr>
                                                <tr>
                                                    <td></td>
                                                    <td>&emsp;</td>
                                                    <td>{parseInt(dashboard?.totalInWomen - dashboard?.totalOutWomen)}</td>
                                                </tr>
                                                <tr>
                                                    <td><b>DELTA</b></td>
                                                    <td>&emsp;</td>
                                                    <td>{parseInt(dashboard?.totalDeltaIn - dashboard?.totalOutDelta)} </td>
                                                </tr>
                                            </tbody>
                                        </table>
                                    </center>
                            </Card>   
                        </Col>
                        
                    </Row>

                </Col>

                
                {/* <Col
                xs={24}
                md={{ span: 11, offset: 2 }}
                className="subCase-card"
            >
                <p> </p>

                <p> </p>
                <p> </p>
                <p> </p>
                <p> </p>
            </Col> */}


                {/* <Col
                    xs={24}
                    style={{ marginTop: 20 }}
                >
                    <Title level={5}>
                         {" "}

                        <PlusCircleOutlined className="link" onClick={() => setOpenAddSub(true)} />
                    </Title>

                </Col> */}
                <Col
                xs={24}
                md={24}
                style={{ margin: "30px 0 30px 0" }}
            >
                <Row>
                <Col
                    xs={24}
                    md={8}
                >
                    <Row>
                            <Col
                                    xs={24}
                                    md={6}
                                >
                                    <Button
                                        type="primary"
                                        onClick={() => setOpenAddSub(true)}
                                    >
                                        
                                        <PlusOutlined />
                                    </Button>
                                </Col>
                                <Col
                                    xs={24}
                                    md={18}
                                >
                                    <Space>
                                        <ExportToExcel 
                                            headers={reportSubHospital} 
                                            data={
                                                subHospitalExport(subHospitalData?.personalInfos, hospitalData)
                                            } 
                                            filename={"sampleTest"} 
                                            title={"Export"}
                                        />
                                        <Select placeholder="" onChange={(e) => setLimit(e)}>
                                            <Option value={10}>10</Option>
                                            <Option value={100}>100</Option>
                                            <Option value={200}>200</Option>
                                            <Option value={500}>500</Option>
                                            <Option value={1000}>1000</Option>
                                        </Select>
                                        <ExportToExcel 
                                            headers={reportSubHospitalWorker} 
                                            data={workerData} 
                                            filename={"worker"} 
                                            title={""}
                                        />

                                        <ExportToExcel 
                                            headers={reportSubHospitalWorker1} 
                                            data={workerData1} 
                                            filename={"worker1"} 
                                            title={"1"}
                                        />
                                    </Space>
                                </Col>
                            </Row>
                        </Col>
                        <Col
                            xs={24}
                            md={16}
                        >
                            
                            <Row>
                                <Col 
                                    xs={24} 
                                    md={4}
                                >
                                    <RangePicker 
                                        // onChange={(e) => setFilterDate(e)}  
                                        onChange={e => onSelectDate(e)} 
                                        placeholder={dateHolder} 
                                        style={{width:"100%"}} 
                                    />
                                </Col>

                                <Col 
                                    xs={24} 
                                    md={{span:4, offset: 1}}
                                >
                                    <DatePicker placeholder="" onChange={(e)=> setNextTest(e)} style={{ width: "100%" }} />
                                </Col>        

                                <Col
                                    xs={24}
                                    md={{span:4, offset: 1}}
                                >
                                    <Select style={{ width: "100%" }} placeholder="" onChange={(e)=> setPatientType(e)}>
                                        <Option value="">----</Option>
                                        <Option value=""></Option>
                                        <Option value=""></Option>
                                        <Option value=""></Option>
                                        <Option value=""></Option>
                                        {/* <Option value=""></Option> */}
                                    </Select>
                                   
                                </Col>

                                <Col
                                    xs={24}
                                    md={{span:4, offset: 1}}
                                >
                                    <Select style={{ width: "100%" }} placeholder="" onChange={(e)=> setTestCount(e)}>
                                        <Option value={null}>----</Option>
                                        <Option value={1}>1</Option>
                                        <Option value={2}>2</Option>
                                        <Option value={3}>3</Option>
                                        <Option value={4}>4</Option>
                                        <Option value={5}>5</Option>
                                    </Select>
                                   
                                </Col>

                                <Col
                                    xs={24}
                                    md={{ span: 4, offset: 1 }}
                                >
                                    <Input.Search
                                        onChange={(e) => setKeyword(e.target.value)}
                                        placeholder="..."
                                    />
                                </Col>
                    </Row>
                </Col>
                </Row>
                </Col>
                <Col
                    xs={24}
                    md={24}
                >
                    <Table
                        columns={subHospitalCol({ handleOpenEditCurrentState, handleDelete, limit, page, hospitalName:hospitalData?.hospitalName,hospitalId:id, handleEditSubHospital, setOpenPrintConfirmFn})}
                        dataSource={personalDataWithNo}
                        rowKey={record => record.id}
                        pagination={{
                            total: subHospitalData?.paginator?.totalDocs,
                            // showSizeChanger: true,
                            pageSize:limit,
                            onChange: ((page, pageSize) => { setPage(page); setLimit(pageSize) })
                        }}
                        scroll={{ x: 900 }}
                        sticky
                        loading={loadingSub}
                        // expandRowByClick={true}
                        expandable={{
                        
                            expandedRowRender: record => (
                            <>
                            <Button
                                type="secondary"
                                onClick={() => handleAddSampleTest({personalInfoId:record.id, lastName:record.lastName})}
                            >
                                        
                                <IdcardOutlined />
                            </Button>
                            <List
                                
                                style={{margin:0}}
                                dataSource={record.sampleTest}
                                renderItem={item => (
                                    
                                    <List.Item>
                                        <Row>
                                            
                                            <Col
                                                xs={24}
                                                md={12}
                                            >
                                                <Typography.Text type={item.result ? "danger" : "success"}>[{item.result ? "" : ""}]  </Typography.Text>
                                                {item.covidVariant}&emsp;<br/>
                                                &emsp;{item.symptomStart === null || item.symptomStart === undefined ? "" : moment.parseZone(item.symptomStart).format("DD MM YYYY")}<br/>
                                                &emsp;{item.date === null || item.date === undefined ? "" : moment.parseZone(item.date).format("DD MM YYYY")}<br/>
                                                &emsp;{item.resultDate === null || item.resultDate === undefined ? "" : moment.parseZone(item.resultDate).format("DD MM YYYY")}<br/>
                                                &emsp;{item.testType}<br/>
                                                &emsp;{item.times}<br/>
                                            </Col>
                                            <Col
                                                xs={24}
                                                md={12}
                                            >
                                                <br/>
                                                &emsp;{item.specimentType}<br/>
                                                &emsp;{item.testLocation}<br/>
                                                &emsp;{item.laboratory}<br/>
                                                &emsp;{item.labFormCompletedBy}<br/>
                                                &emsp;{item.labFormCompletedByTel}<br/><br/>
                                                </Col>
                                                <Col
                                                    xs={24}
                                                    md={24}
                                                >
                                                    <br/>
                                                    <Button onClick={()=> {handleLabForm({personData:record,labData:item})}}><CopyOutlined  style={{ color: "green" }}/></Button>
                                                    <Button onClick={()=> handleEdit({...item,personalInfoId:record.id,fullName:record.lastName+" "+record.firstName})}><EditOutlined  style={{ color: "green" }}/></Button>
                                                    <Popconfirm
                                                        title="?"
                                                        onConfirm={ ()=>handleDeleteSampleTest({sampleTestId:item.id,personalInfoId:record.id})}
                                                        okText=""
                                                        cancelText=""
                                                    >
                                                        <Button><DeleteOutlined style={{ color: "red" }} /></Button>
                                                    </Popconfirm>
                                                </Col>
                                        </Row>
                                    </List.Item>
                                    )}
                            />
                           </>
                           ),
                          }}
                        // expandedRowKeys
                        // expandable={{
                            
                        //     expandedRowRender: record => (
                        //     <List
                        //         // bordered
                        //         dataSource={record.hospitalizations}
                        //         renderItem={item => (
                        //             <List.Item>
                        //                 <Row>
                        //                     <Col
                        //                         xs={24}
                        //                         md={24}
                        //                     >
                        //                         <Typography.Text type={item.coorporate ? "success": "danger" }>[{item.coorporate ? "":""}]  </Typography.Text><br/>
                        //                         &emsp;{item.covidVariant}<br/>
                        //                         &emsp;{moment(item.date_in).format("DD MM YYYY")}<br/>
                        //                         &emsp;{item.date_out===null || item.date_out===undefined ? "":moment(item.date_out).format("DD MM YYYY")}<br/>
                        //                         &emsp;{item.description}<br/><br/>
                        //                         </Col>
                        //                         <Col
                        //                             xs={24}
                        //                             md={24}
                        //                         >
                        //                             <br/>
                        //                             <Button onClick={()=> {}}><CopyOutlined  style={{ color: "green" }}/></Button>
                        //                             <Button onClick={()=> {handleEditSubHospital({...item,personalInfoId:record.id})}}><EditOutlined  style={{ color: "green" }}/></Button>
                        //                             <Popconfirm
                        //                                 title="?"
                        //                                 onConfirm={ ()=>{handleDelete(item.id, record.id)}}
                        //                                 okText=""
                        //                                 cancelText=""
                        //                             >
                        //                                 <Button><DeleteOutlined style={{ color: "red" }} /></Button>
                        //                             </Popconfirm>
                        //                             <Button onClick={()=> {}}><FileExcelOutlined  style={{ color: "red" }}/></Button>
                        //                         </Col>
                        //                 </Row>
                        //             </List.Item>
                        //             )}
                        //         />
                        //     ),
                        // }}
                    />
                </Col>

                <Modal
                    width={1100}
                    visible={openPrintConfirm}
                    footer={[
                        <Button
                            key="add"
                            type="primary"
                            onClick={() => setOpenAddMore(true)}
                        >
                            
                        </Button>,
                        <ReactToPrint
                            key="print"
                            trigger={() => <Button type="primary" ></Button>}
                            content={() => printConfirmRef.current}
                        />
                    ]}
                    onCancel={() => setOpenPrintConfirm(false)}
                >
                    <ReportConfirm ref={printConfirmRef} data={printData} moreData={addMore} />
                </Modal>

                <AddMoreHospital open={openAddMore} setOpen={setOpenAddMore} addMore={addMore} setAddMore={setAddMore} />
            </Row>
        </>
    )
}
