import { Space, Popconfirm,Button,Tooltip } from 'antd'
import {
    EditOutlined,
    DeleteOutlined,
    EyeOutlined,
    CopyOutlined,
    FileExcelOutlined,
    AuditOutlined,
    FundViewOutlined

} from '@ant-design/icons';
import { Link } from 'react-router-dom';
import moment from 'moment';

export const subHospitalCol = ({handleOpenEditCurrentState,handleDelete,hospitalName,hospitalId,handleEditSubHospital, limit,page, setOpenPrintConfirmFn}) => {
    var no = ((page-1) * limit)
    var array = [
        {
            title: '.',
            dataIndex: 'no',
            key: 'no',
            width: 110,
            // render: (text, record, i) => (
            //     <Space size="middle">
            //         {no += 1}
            //     </Space>
            // ),
        },
        {
            title: '',
            dataIndex: 'name',
            key: 'name',
            width: 100,
            render: (text, record) => (
                <span>
                    {record?.lastName} {record?.firstName}
                </span>
            ),
        },
        {
            title: '',
            dataIndex: 'gender',
            key: 'gender',
            width: 50,
            render: (text, record) => (
                <span>
                    {record?.gender}
                </span>
            ),
        },
        {
            title: '',
            dataIndex: 'age',
            key: 'age',
            width: 40,
            render: (text, record) => (
                <span>
                    {record?.age}
                </span>
            ),
        },
        {
            title: '',
            dataIndex: 'phone',
            key: 'phone',
            width: 60,
            render: (text, record) => (
                <span>
                    {record?.tel}
                </span>
            ),
        },

        {
            title: '',
            dataIndex: 'date_in',
            key: 'date_in',
            width: 100,
            render: (text, record) => {
                let hospitalLength = record?.hospitalizations?.length
                let hospitalization = record?.hospitalizations[hospitalLength-1]
                return(
                <Space size="middle">
                    {hospitalization.date_in === null ? "": moment.parseZone(hospitalization.date_in).format("DD MM YYYY")}
                    {/* {moment.parseZone(hospitalization?.date_in).format("DD MM YYYY")} */}
                </Space>
            )
            }
        },

        {
            title: '',
            dataIndex: 'date_out',
            key: 'date_out',
            width: 100,
            render: (text, record) => {

                // let hospitalization = record?.hospitalizations?.filter(e=> e.hospitalName === hospitalName  )
                let hospitalLength = record?.hospitalizations?.length
                let hospitalization = record?.hospitalizations[hospitalLength-1]

                return(
                <span size="middle">
                    {hospitalization.date_out === null ? "": moment.parseZone(hospitalization.date_out).format("DD MM YYYY")}
                </span>
                )
            },
        },

        {
            title: '',
            dataIndex: 'date_follow',
            key: 'date_follow',
            width: 100,
            render: (text, record) => {

                // let hospitalization = record?.hospitalizations?.filter(e=> e.hospitalName === hospitalName  )
                let hospitalLength = record?.hospitalizations?.length
                let hospitalization = record?.hospitalizations[hospitalLength-1]

                return(
                <span size="middle">
                    {hospitalization?.nextSampleTestDate === null ? "": moment.parseZone(hospitalization?.nextSampleTestDate).format("DD MM YYYY")}
                  
                </span>
                )
            },
        },
      
        {
            title: '',
            dataIndex: 'address',
            key: 'address',
            width: 150,
            render: (text, record) => (
                <span size="middle">
                    {(record?.village !== "") && record?.village+","}
                    {record?.commune !== "" && record?.commune+","}
                    {record?.district !== "" && record?.district+","}
                    {record?.province && record?.province}
                </span>
            ),
        },

        {
            title: '',
            dataIndex: 'test_count',
            key: 'test_count',
            width: 100,
            render: (text, record) => (
                <span size="middle">
                    {record?.sampleTest?.length} 
                </span>
            ),
        },

        {
            title: '',
            dataIndex: 'status',
            key: 'status',
            width: 90,
            render: (text, record) => {

                let hospitalLength = record?.hospitalizations?.length
                let arr = record?.hospitalizations[hospitalLength-1]

                var dayOut = moment.parseZone(arr?.date_out).format("YYYY-MM-DD");
                var dayNow = moment.parseZone(new Date()).format("YYYY-MM-DD");

                let state ="";
                let classN = "badge-purple"

                var dayNextTest = moment.parseZone(arr?.nextSampleTestDate).format("YYYY-MM-DD");

                if(arr.nextSampleTestDate!==null &&  dayNextTest == dayNow){
                    state = ""
                    classN= "badge-warning" 
                }

                if(arr.date_out!==null &&  dayOut < dayNow){
                    state = ""
                    classN= "badge-success"
                }

                // if(arr.date_out!==null &&  dayNextTest == dayNow){
                //     state = ""
                //     classN= "badge-purple"
                // }

                return (
                    <span className={classN}>{state}</span>
                )
            }
         
        },
        // {
        //     title: '',
        //     dataIndex: 'relatedInfo',
        //     key: 'relatedInfo',
        //     width: 30,
        //     render: (text, record) => (
        //         <Space size="middle">
        //             {record?.personalInfo?.direct ? "":""}
        //         </Space>
        //     ),
        // },
        {
            key: 'action',
            dataIndex: 'action',
            fixed: 'right',
            width: 120,
            align: 'center',
            render: (text, record) => {
                let hospitalization = record?.hospitalizations?.filter(e => e.hospitalInfo===hospitalId)
                // let hospitalization = record?.hospitalizations[]

                return(
                <Space size="middle">
                    {/* <CopyOutlined  style={{ color: "green" }}/> */}
                    <Tooltip title="">
                        <EditOutlined onClick={()=> {handleEditSubHospital({...hospitalization[hospitalization?.length - 1],personalInfoId:record.id})}} style={{ color: "green" }}/>
                    </Tooltip>

                    <Tooltip title="">
                        <FundViewOutlined onClick={()=> {handleOpenEditCurrentState({...record?.currentState,personalInfoId:record.id})}} style={{ color: "green" }}/>
                    </Tooltip>

                    <Popconfirm
                        title="?"
                        onConfirm={ ()=>{handleDelete(hospitalization[hospitalization?.length - 1].id, record.id)}}
                        okText=""
                        cancelText=""
                    >
                        <Tooltip title="">
                            <DeleteOutlined style={{ color: "red" }} />
                        </Tooltip>
                    </Popconfirm>
                    {record?.currentState?.recovered === true ?
                    <Tooltip title="">
                        <AuditOutlined onClick={()=> {setOpenPrintConfirmFn(record)}} className="link"/>
                    </Tooltip>
                    : null}
                    <Tooltip title="">
                        <Link className="link" to={"/subPeople/" + record?.id}><EyeOutlined /></Link>
                    </Tooltip>
                </Space>
                )
            },
        }
    ]
    return array
}