import React, {useContext, useState,useEffect} from 'react'
import { Row, Col, Button, Input, Table, message,Select, DatePicker,Form, Checkbox } from 'antd'
import { PlusOutlined } from '@ant-design/icons';
import { PeopleController } from '../context/peopleContext';
import { peopleCol } from '../component/people/tableColumn/peopleColumn';
import AddPeople from '../component/people/modal/addPeople';
import { useQuery,useMutation } from '@apollo/client';
import { 
    DELETE_PERSONALINFO_BY_ID, 
    GET_ALL_PERSONINFO ,
    GET_ALL_PERSONINFO_FILTER,
    GET_ALL_PERSONINFO_SAMPLE
} from '../graphql/people';
import { DELETE_HOSPITALINFO_BY_ID } from '../graphql/hospital';
import { useLocation, useRouteMatch } from 'react-router-dom';
import moment from 'moment';
import ExportToExcel from "../component/export/exportToExcel";
import {PeopleList} from '../asset/exportExcel/reportExcel'
import {convertForPeopleExcel, convertPersonalToSelect, convertToCommune, convertToDistrict, convertToVillage, convertUserToSelect} from '../function/fn'
import { ListSelect, ListSelectTab } from '../static/own-comp';
import { provinceData, districtData, communeData, villageData } from '../context/headerContext'
import { ALL_USERS } from '../graphql/auth';

const {Option} = Select
const {RangePicker} = DatePicker

export default function FilterPeople() {
    let location = useLocation()

    let [form] = Form.useForm()

    const [covidTypeState,setCovidTypeState] = useState(true)
    const [peopleData, setPeopleData] = useState({})

    const [openAdd, setOpenAdd] = useState(false)
    const [page, setPage] = useState(1)
    const [limit, setLimit] = useState(15)
    const [keyword, setKeyword] = useState("")
    const [filterCurrentState, setCurrentState] = useState(null)
    const [covidType,setCovidType] = useState("")
    // const [peopleData, setPeopleData] = useState([])
    const [ranges, setRanges] = useState({
        startDate: null,
        endDate: null,
    })   

    const [allUserInfo, setAllUser] = useState([])

    const [province, setProvince] = useState("")
    const [district, setDistrict] = useState("")
    const [commune, setCommune] = useState("")

    const [confirm, setConfirm] = useState(true);
    const [recovered, setRecovered] = useState(false);
    const [death, setDeath] = useState(false);

    const [filterData,setFilterData] = useState({
        firstName:null,
        lastName:null,
        covidType:null,
        village:null,
        commune:null,
        district:null,
        province:null,
        confirm:true,
        death:false,
        recovered:false,
        createdthisBy:null,
        updatedthisBy:null,
        createdSampleTestBy:null,
        createdSampleTestupdatedBy:null,
        createStartDate:null,
        createEndDate: null,
        confirmStartDate:null,
        confirmEndDate:null,
        recoveredStartDate:null,
        recoveredEndDate:null,
        deathStartDate:null,
        deathEndDate:null,
        sampleTestStartDate:null,
        sampleTestEndDate:null
    })

    // const {data:personalInfo} = useQuery(GET_ALL_PERSONINFO_SAMPLE,{
    //     fetchPolicy:"network-only",
    //     nextFetchPolicy:"cache-only"
    // })

    // const getPersonalInfo = personalInfo?.allPersonalInfos

    useEffect(() => {
        if(location?.state !== undefined ){
            setCurrentState(location?.state?.status)

            if(location?.state?.status ===""){
                setCovidTypeState(false)
                setCovidType(location?.state?.covidType)
            }
            
            setRanges({
                startDate:location?.state?.date,
                endDate:location?.state?.date
            })
        }
        // console.log(location)
    }, [location])

    const {data,loading,error,refetch} = useQuery(GET_ALL_PERSONINFO_FILTER,{
        variables:{...filterData,
            page:page,
            limit:limit,
            
        },
        onCompleted:({getPersonalInfoWithPaginations})=>{
            setPeopleData(getPersonalInfoWithPaginations)
        },
        onError:(error)=>{
            message.error(error.message)
        },
        fetchPolicy:'network-only',
        nextFetchPolicy:"cache-only",
    })

    // useEffect(() => {
    //     if(location?.state !== undefined ){
    //         setCurrentState(location?.state?.status)

    //         if(location?.state?.status ===""){
    //             setCovidTypeState(false)
    //             setCovidType(location?.state?.covidType)
    //         }
            
    //         setRanges({
    //             startDate:location?.state?.date,
    //             endDate:location?.state?.date
    //         })
    //     }
    //     // console.log(location)
    // }, [location])

    // useEffect(() => {
    //     refetch()
    // }, [page,limit,keyword,filterCurrentState,covidType])
    useEffect(()=>{
        if(data!==undefined){
            setPeopleData(data?.getPersonalInfoWithPaginations)

        }
    },[data])

    const {data:allUser} = useQuery(ALL_USERS,{
        onCompleted:({allUsers})=>{
            setAllUser(allUsers)
        },
        onError:(error)=>{
            message.error(error.message)
        },
        fetchPolicy:'network-only',
        nextFetchPolicy:"cache-only",
    })

    useEffect(()=>{
        if(allUser!==undefined){
            setAllUser(allUser?.allUsers)
        }
    },[allUser])

    const [deletePersonalInfo,{loading:deleteLoading, error:deleteEorror}] = useMutation(DELETE_PERSONALINFO_BY_ID,{
        onCompleted:()=>{
            refetch()
            message.success("")
        },
        onError:(errors)=>{
            message.error(errors.message)
        }
    })

    const handleDelete = (e) => {
        deletePersonalInfo({
            variables:{
                id:e
            }
        })
    }

    const setFilterCurrentState=(e)=>{
        // console.log(e)
        setCurrentState(e)
        if(e ===""){
            setCovidTypeState(false)
        }else{
            setCovidType("")
            setCovidTypeState(true)
        }
    }

    const setFilterCovidType=(e)=>{
        // console.log(e)
        setCovidType(e)
    }

    const onSelectDate = (e) => {
        if(e !== null){
            let startDate = moment(e[0])
            let endDate = moment(e[1])

            setRanges({
                startDate: startDate,
                endDate: endDate
            })
        }else{
            setRanges({
                startDate: null,
                endDate: null
            })
        }
    }

    const dateHolder = ["", ""]
    const dateSampleHolder = ["", ""]
    const dateConfirmHolder = ["", ""]

    const dateRecoveredHolder = ["", ""]
    const dateDeathHolder = ["", ""]

    const onFinish = (values) => {

        console.log(values)
        setFilterData({
            ...values,
            firstName:values.firstName!==undefined ? values.firstName:null,
            lastName:values.lastName!==undefined ? values.lastName:null,
            province:values.province!==undefined ? values.province:null,
            district:values.district!==undefined ? values.district:null,
            commune:values.commune!==undefined ? values.commune:null,
            village:values.village!==undefined ? values.village:null,
            confirm: values.confirm ? true : false,
            recovered: values.recovered ? true : false,
            death: values.death ? true : false,
            createStartDate: values.createDate !==undefined && values.createDate !==null ? moment.parseZone(values.createDate[0]):null,
            createEndDate: values.createDate !==undefined && values.createDate !==null ? moment.parseZone(values.createDate[1]):null,
            sampleTestStartDate: values.sampleTestDate !==undefined && values.sampleTestDate !==null  ? moment.parseZone(values.sampleTestDate[0]):null,
            sampleTestEndDate: values.sampleTestDate !==undefined && values.sampleTestDate !==null ? moment.parseZone(values.sampleTestDate[1]):null,

            confirmStartDate: values.confirmedAt !==undefined && values.confirmedAt !==null ? moment.parseZone(values.confirmedAt[0]):null,
            confirmEndDate: values.confirmedAt !==undefined && values.confirmedAt !==null ? moment.parseZone(values.confirmedAt[1]):null,
            recoveredStartDate: values.recoveredAt !==undefined && values.recoveredAt !==null ? moment.parseZone(values.recoveredAt[0]):null,
            recoveredEndDate: values.recoveredAt !==undefined && values.recoveredAt !==null ? moment.parseZone(values.recoveredAt[1]):null,
            deathStartDate: values.deathAt !==undefined && values.deathAt !==null ? moment.parseZone(values.deathAt[0]):null,
            deathEndDate: values.deathAt !==undefined && values.deathAt !==null ? moment.parseZone(values.deathAt[1]):null,

            createdthisBy:values.createdthisBy!==undefined ? values.createdthisBy:null,
            updatedthisBy:values.updatedthisBy!==undefined ? values.updatedthisBy:null,
            createdSampleTestBy:values.createdSampleTestBy!==undefined ? values.createdSampleTestBy:null,
            createdSampleTestupdatedBy:values.createdSampleTestupdatedBy!==undefined ? values.createdSampleTestupdatedBy:null,
        })


    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    const setToProviceFn = (e) => {
        form.setFieldsValue({
            province: e,
            district: null,
            commune: null,
            village: null,
        });

        setProvince(e)
        setDistrict("")
        setCommune("")
    };


    const setToDistrictFn = (e) => {
        form.setFieldsValue({
            district: e,
            commune: null,
            village: null,
        });

        setDistrict(e)
        setCommune("")
    };

    const setToCommuneFn = (e) => {
        form.setFieldsValue({
            commune: e,
            village: null,
        });

        setCommune(e)
    };

    const setToVillageFn = (e) => {
        form.setFieldsValue({
            village: e
        });
    };

    const handleCheckConfirm=(e)=>{
        
        setConfirm(e)
        if(!e){
            form.setFieldsValue({
                confirmedAt:null,
                covidType:null 
            })
        }
    }

    const handleCheckRecover=(e)=>{
        
        setRecovered(e)
        if(!e){
            form.setFieldsValue({
                recoveredAt:null
            })
        }
    }

    const handleCheckDeath=(e)=>{
        
        setDeath(e)
        if(!e){
            form.setFieldsValue({
                deathAt:null
            })
        }
    }

    const setToUpdatedByFn = (e) => {
        form.setFieldsValue({
            updatedthisBy: e,
        });

    };

    const setToCreatedByFn = (e) => {
        form.setFieldsValue({
            createdthisBy: e,
        });

        // setPersonalInfoIdRelated(e);
    };

    const setToUpdatedSampleTestByFn = (e) => {
        form.setFieldsValue({
            updatedSampleTestBy: e,
        });

    };

    const setToCreatedSampleTestByFn = (e) => {
        form.setFieldsValue({
            createdSampleTestBy: e,
        });

    };

    // get no for location data
    let peopleDataWithNo = []
    peopleData?.personalInfos?.map((record,index) =>{
        let pageAdd = peopleData?.paginator?.currentPage > 1 ? ((peopleData?.paginator?.currentPage*limit)-limit)+1: 1;

        let setNoToPeopleData = {...record,no:index+pageAdd}
        peopleDataWithNo.push(setNoToPeopleData)

    })

    return (
        <Row>
            {/* <AddPeople open={openAdd} setOpen={setOpenAdd} setRefetch={refetch}  /> */}
            {/* <Col
                xs={24}
                md={4}
            >
                <Button
                    type="primary"
                    onClick={() => setOpenAdd(true)}
                >
                    
                    <PlusOutlined />
                </Button>
            </Col> */}
            {/* <Col
                xs={24}
                md={3}
            >
                <ExportToExcel 
                    headers={PeopleList} 
                    data={
                        convertForPeopleExcel(getPersonalInfoWithPagination?.personalInfos === undefined ? [] : getPersonalInfoWithPagination?.personalInfos)
                    } 
                    filename={"sampleTest"} 
                    title={"Export to CSV"}
                />
            </Col> */}
            {/* <Col
                xs={24}
                md={3}
            >
                <Select placeholder="" onChange={(e)=> setLimit(e)}>
                    <Option value={100}>100</Option>
                    <Option value={200}>200</Option>
                    <Option value={300}>300</Option>
                    <Option value={400}>400</Option>
                    <Option value={500}>500</Option>
                    <Option value={600}>600</Option>

                 
                    </Select>
            </Col> */}
            <Form
                form={form}
                name="addSubHospital"
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
                style={{width:'100%'}}
                fields={
                    [
                        {
                            name:"confirm",
                            value:confirm,
                        }
                    ]
                }
            >
                <Row>
                    <Col
                        xs={24}
                        md={{span:4}}
                        style={{padding:5}}
                    >
                        <Form.Item
                            name="lastName"
                            // rules={[{ required: true, message: '!' }]}
                        >
                        <Input
                            // onChange={(e)=>setKeyword(e.target.value)}
                            placeholder=""
                        />
                        </Form.Item>
                    </Col>

                    <Col
                        xs={24}
                        md={{span:4}}
                        style={{padding:5}}
                    >
                        <Form.Item
                            name="firstName"
                            // rules={[{ required: true, message: '!' }]}
                        >
                        <Input
                            // onChange={(e)=>setKeyword(e.target.value)}
                            placeholder=""
                        />
                        </Form.Item>
                    </Col>

                    <Col
                        xs={24}
                        md={{span:4}}
                        style={{padding:5}}
                    >
                        <Form.Item
                            name="createdthisBy"
                            // rules={[{ required: true, message: '!' }]}
                        >
                        <ListSelectTab
                            type={6}
                            data={convertUserToSelect(allUserInfo)}
                            title=""
                            setValue={setToCreatedByFn}
                        />
                        </Form.Item>
                    </Col>

                    <Col
                        xs={24}
                        md={{span:4}}
                        style={{padding:5}}
                    >
                        <Form.Item
                            name="updatedthisBy"
                            // rules={[{ required: true, message: '!' }]}
                        >
                        {/* <SelectPeople disabled={false} data={convertPersonalToSelect(personalAllData)} title="" handleSearchPeople={handleSearchPeople} setValue={setToPersonalInfoFn} /> */}
                        {/* <Select placeholder="updatedBy" style={{ width: "100%" }}>
                            <Option value="">----</Option>
                            <Option value=""></Option>
                            <Option value=""></Option>
                            <Option value=""></Option>
                            <Option value=""></Option>
                        </Select> */}

                        <ListSelectTab
                            type={6}
                            data={convertUserToSelect(allUserInfo)}
                            title=""
                            setValue={setToUpdatedByFn}
                        />
                        </Form.Item>
                    </Col>

                    <Col
                        xs={24}
                        md={{span:4}}
                        style={{padding:5}}
                    >
                        <Form.Item
                            name="createdSampleTestBy"
                            // rules={[{ required: true, message: '!' }]}
                        >
                        <ListSelectTab
                            type={6}
                            data={convertUserToSelect(allUserInfo)}
                            title=""
                            setValue={setToCreatedSampleTestByFn}
                        />
                        </Form.Item>
                    </Col>

                    <Col
                        xs={24}
                        md={{span:4}}
                        style={{padding:5}}
                    >
                        <Form.Item
                            name="updatedSampleTestBy"
                            // rules={[{ required: true, message: '!' }]}
                        >
                        <ListSelectTab
                            type={6}
                            data={convertUserToSelect(allUserInfo)}
                            title=""
                            setValue={setToUpdatedSampleTestByFn}
                        />
                        </Form.Item>
                    </Col>

                    {/* <Col 
                        xs={24}
                        md={{span:4}}
                        >
                        <Form.Item
                            name="covidVariant"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <RangePicker 
                                // onChange={(e) => setFilterDate(e)}  
                                defaultValue={[location?.state?.date===null ? null :moment(location?.state?.date),location?.state?.date===null ? null :moment(location?.state?.date)]}
                                onChange={e => onSelectDate(e)} 
                                placeholder={dateHolder}
                                style={{width:"100%"}} 
                            />
                        </Form.Item>
                    </Col>
                    <Col 
                        xs={24}
                        md={{span:4}}
                        >
                        <Form.Item
                            name="covidVariant"
                            rules={[{ required: true, message: '!' }]}
                        >
                        <Select defaultValue={location?.state?.status} placeholder="" style={{ width: "100%" }} onChange={(e)=> setFilterCurrentState(e)}>
                            <Option value="">----</Option>
                            <Option value=""></Option>
                            <Option value=""></Option>
                            <Option value=""></Option>
                            <Option value=""></Option>
                        </Select>
                        </Form.Item>
                    </Col> */}

                    <Col 
                        xs={24}
                        md={{span:4}}
                        style={{padding:5}}
                        >
                        <Form.Item
                            name="createDate"
                            // rules={[{ required: true, message: '!' }]}
                        >
                            <RangePicker 
                                // onChange={(e) => setFilterDate(e)}  
                                //defaultValue={[location?.state?.date===null ? null :moment(location?.state?.date),location?.state?.date===null ? null :moment(location?.state?.date)]}
                                // onChange={e => onSelectDate(e)} 
                                placeholder={dateHolder}
                                style={{width:"100%"}} 
                            />
                        </Form.Item>
                    </Col>

                    <Col xs={24}
                        md={{span:4}} style={{padding:5}}>
                        <Form.Item
                            name="province"
                            // rules={[{ required: true, message: '!' }]}
                        >
                            <ListSelect type={1} data={provinceData} title="/" setValue={setToProviceFn} />
                        </Form.Item>
                    </Col>
                    <Col xs={24}
                        md={{span:4}} style={{padding:5}}>
                        <Form.Item
                            name="district"
                            // rules={[{ required: true, message: '!' }]}
                        >
                            <ListSelect type={0} data={convertToDistrict(districtData)} title="/" setValue={setToDistrictFn} disabled={province !== "" ? true : false}/>
                        </Form.Item>
                    </Col>
                    <Col xs={24}
                        md={{span:4}} style={{padding:5}}>
                        <Form.Item
                            name="commune"
                            // rules={[{ required: true, message: '!' }]}
                        >
                            <ListSelect type={1} data={convertToCommune(district, communeData)} title="/" setValue={setToCommuneFn} disabled={province !== "" ? true : false}/>
                        </Form.Item>
                    </Col>
                    <Col xs={24}
                        md={{span:4}} style={{padding:5}}>
                        <Form.Item
                            name="village"
                            // rules={[{ required: true, message: '!' }]}
                        >
                            <ListSelect type={1} data={convertToVillage(commune, villageData)} title="" setValue={setToVillageFn} disabled={province !== "" ? true : false}/>
                        </Form.Item>
                    </Col>

                    <Col 
                        xs={24}
                        md={{span:4}}
                        style={{padding:5}}
                        >
                        <Form.Item
                            name="sampleTestDate"
                            // rules={[{ required: true, message: '!' }]}
                        >
                            <RangePicker 
                                // onChange={(e) => setFilterDate(e)}  
                                //defaultValue={[location?.state?.date===null ? null :moment(location?.state?.date),location?.state?.date===null ? null :moment(location?.state?.date)]}
                                // onChange={e => onSelectDate(e)} 
                                placeholder={dateSampleHolder}
                                style={{width:"100%"}} 
                            />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 4, offset: 0 }} style={{padding:5}}>
                        <Form.Item name="confirm" valuePropName="checked">
                            <Checkbox onChange={(e) => handleCheckConfirm(e.target.checked)}>
                                
                            </Checkbox>
                        </Form.Item>
                        <Form.Item
                            name="confirmedAt"
                            // rules={[{ required: true, message: '!' }]}
                        >
                            <RangePicker
                            style={{ width: "100%" }}
                            placeholder={dateConfirmHolder}
                            disabled={!confirm}
                            />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 4, offset: 0 }} style={{padding:5}}>
                        <Form.Item name="recovered" valuePropName="checked">
                            <Checkbox onChange={(e) => handleCheckRecover(e.target.checked)}>
                                
                            </Checkbox>
                        </Form.Item>
                        <Form.Item
                            name="recoveredAt"
                            // rules={[{ required: true, message: '!' }]}
                        >
                            <RangePicker
                            style={{ width: "100%" }}
                            placeholder={dateRecoveredHolder}
                            disabled={!recovered}
                            />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 4, offset: 0 }} style={{padding:5}}>
                        <Form.Item name="death" valuePropName="checked">
                            <Checkbox onChange={(e) => handleCheckDeath(e.target.checked)}>
                                
                            </Checkbox>
                        </Form.Item>
                        <Form.Item
                            name="deathAt"
                            // rules={[{ required: true, message: '!' }]}
                        >
                            <RangePicker
                            style={{ width: "100%" }}
                            placeholder={dateDeathHolder}
                            disabled={!death}
                            />
                        </Form.Item>
                    </Col>

                    <Col 
                        xs={24}
                        md={{span:4}}
                        style={{padding:5}}
                        >
                        <Form.Item  
                            name="covidType"
                        >
                        <Select disabled={!confirm} placeholder="" style={{ width: "100%" }}>
                            <Option value="">----</Option>
                            <Option value="COVID-19">COVID-19</Option>
                            <Option value="DELTA">DELTA</Option>
                            <Option value="ALPHA">ALPHA</Option>
                            {/* <Option value=""></Option> */}
                        </Select>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{span:4}} style={{padding:5}}>
                        <Button htmlType="submit" type="primary" style={{ width: "100%" }}>
                        
                        </Button>
                    </Col>
                    
                </Row>
            </Form>
            <Col
                xs={24}
                style={{ marginTop: 20 }}
            >
                <Table
                    className="table-go-list"
                    columns={peopleCol({handleDelete,limit,page})}
                    dataSource={peopleDataWithNo}
                    rowKey={record => record.id}
                    pagination={{
                        total: peopleData?.paginator?.totalDocs,
                        //pageSizeOptions:["10", "20"],
                        // showSizeChanger: true,
                        pageSize:limit,
                        // current: getPersonalInfoWithPagination?.paginator?.currentPage,
                        onChange: ((page, pageSize) => { setPage(page); setLimit(pageSize) })
                    }}
                    scroll={{ x: 1000 }} 
                    sticky 
                    loading={loading}
                    
                />
            </Col>
        </Row>
    )
}
