import React, { Component } from 'react'
import Logo from '../../asset/srLogo.png'
import {Row, Col} from 'antd'
import {getKhmerDate} from '../../function/fn'

export default class ReportConfirm extends Component {
    render() {
        let personalInfo = this.props.data
        console.log(this.props.moreData)
        return (
            <Row
                style={{
                    padding: 50
                }}
            >
                <Col
                    xs={24}
                >
                    <div
                            style={{
                                textAlign: "center",
                                // width: 300,
                                // float: 'right'
                                // backgroundColor: "blue"
                            }}
                        >
                            <p>
                                <span className="HeaderFont"><br />
                                      

                                </span><br />

                                <span className="HeaderFont1"
                                    style={{
                                        fontSize: "300pt !important"
                                    }}
                                >3</span>
                            </p>
                        </div>
                </Col>
                <Col
                    xs={24}
                >
                    <img src={Logo} width="130" /><br/>
                    <p>
                        <br />
                        <br />
                        <br />
                                           ..
                    </p>
                </Col>
                <Col
                    xs={24}
                >
                    <center>
                        <h3>
                            <b>
                                <u>
                                    
                                </u>
                            </b>
                        </h3>
                    </center>
                </Col>
                <Col
                    xs={24}
                    style={{
                        marginBottom: 50
                    }}
                >
                    <dl>
                        <dd>-&emsp; <b>{personalInfo?.lastName} {personalInfo?.firstName}</b>  <b>{personalInfo?.gender}</b>  <b>{personalInfo?.age} </b></dd>
                        <dd>-&emsp; {personalInfo?.village === "" ? "" : (<span> <b>{personalInfo?.village}</b>  <b>{personalInfo?.commune}</b>  <b>{personalInfo?.district}</b></span>)}  <b>{personalInfo?.province}</b></dd>
                        <dd>-&emsp;  <b>{personalInfo?.nationality}</b>  {personalInfo?.idCard === "" || personalInfo?.idCard === null ? <b></b> : <b>{personalInfo?.idCard}</b>}</dd>
                        <dd>-&emsp; <b>{getKhmerDate(personalInfo?.hospitalizations[0]?.date_in)}</b></dd>
                        <dd>-&emsp; <b>{personalInfo?.hospitalizations[0]?.infection}</b></dd>
                        <dd>-&emsp; <b>{getKhmerDate(personalInfo?.hospitalizations[0]?.date_out)}</b></dd>
                        <dd>-&emsp; <b></b></dd>
                        <dd>-&emsp; 
                                       
                        </dd>
                    </dl>
                </Col>
                <Col
                    xs={24}
                    style={{
                        marginBottom: 50
                    }}
                >
                    <div
                        style={{
                            textAlign: "center",
                            width: 400,
                            float: 'right',
                            // backgroundColor: "blue"
                        }}
                    >
                        <p>
                            <span>
                                {/* <b></b> <b></b>  . <b></b><br />
                                ,<b></b>  <b></b> <b></b><br /> */}
                                {this.props.moreData?.khmerDate}<br />
                                {this.props.moreData?.englishDate}<br />
                                <b></b>
                            </span><br/>
                        </p>
                            <p
                                style={{
                                    marginTop: 100
                                }}
                            >
                                {this.props.moreData?.name}
                            </p>
                    </div>
                </Col>
                <Col
                    xs={24}
                >
                    <p>
                        <br />
                        <b></b>
                    </p>
                </Col>
            </Row>
        )
    }
}
