import React, { useState,useCallback } from 'react'
import { Modal, Form, Row, Col, Button, Upload, message, Image } from 'antd'
import { LoadingOutlined, PlusOutlined } from '@ant-design/icons';
import ImgCrop from 'antd-img-crop';
import {useMutation} from '@apollo/client'
import {IMAGE_URL} from '../../../graphql/upload'
import { UPDATE_PERSON_PROFILE_BY_ID } from '../../../graphql/people';
import Cropper from 'react-easy-crop'

function getBase64(img, callback) {
    const reader = new FileReader();
    reader.addEventListener('load', () => callback(reader.result));
    reader.readAsDataURL(img);
}

function beforeUpload(file) {
    const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
    if (!isJpgOrPng) {
        message.error('You can only upload JPG/PNG file!');
    }
    const isLt2M = file.size / 1024 / 1024 < 2;
    if (!isLt2M) {
        message.error('Image must smaller than 2MB!');
    }
    return isJpgOrPng && isLt2M;
}

export default function UploadPic({ open, setOpen,setRefetch, peopleId }) {

    let [form] = Form.useForm()

    const [fileList, setFileList] = useState([]);
    const [showImage, setShowImage] = useState("")
    const [crop, setCrop] = useState({ x: 150, y: 150 })
  const [zoom, setZoom] = useState(1)

    const [updatePersonalInfo, { loading: loadingCreate, error: errorCreate }] =
        useMutation(UPDATE_PERSON_PROFILE_BY_ID, {
        onCompleted: ({ updatePersonalInfo }) => {
            if(updatePersonalInfo.success===true){
                setRefetch();
                message.success("");
            }else{
                message.error(updatePersonalInfo.message)
            }
            
        },
            onError: (error) => {
            message.error(error.message);
        },
    });

    const uploadToS3 = async (url)=>{
        await fetch(url, {
            method: "PUT",
            headers: {
              "Content-Type": "multipart/form-data"
            },
            body: fileList[fileList.length-1].originFileObj
        })

        const imageUrl = url.split('?')[0]
        setShowImage(imageUrl)
        // console.log(imageUrl)
        updatePersonalInfo({
            variables:{
                id:peopleId,
                profileImg:imageUrl
            }
        })
    }

    const [uploadImageUrl]= useMutation(IMAGE_URL,{
        onCompleted:({uploadImageUrl})=>{
            uploadToS3(uploadImageUrl)
        }
    })

    
    const onFinish = (values) => {
        // console.log('Success:', fileList[fileList.length-1].originFileObj);

        uploadImageUrl()
        setOpen(false)
        form.resetFields()
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    const onChange = ({ fileList: newFileList }) => {
        if(newFileList?.length > 0){
            setFileList([{...newFileList[newFileList?.length -1], status:"done"}]);
        }else{
            setFileList([])
        }
    };

    const onCropComplete = useCallback((croppedArea, croppedAreaPixels) => {
        console.log(croppedArea, croppedAreaPixels)
    }, [])

    const onPreview = async file => {
        console.log(file.url)
        let src = file.url;
        if (!src) {
          src = await new Promise(resolve => {
            const reader = new FileReader();
            reader.readAsDataURL(file.originFileObj);
            reader.onload = () => resolve(reader.result);
          });

        }
        // const image = new Image();
        // image.src = src;
        // const imgWindow = window.open(src);
        // imgWindow.document.write(image.outerHTML);
        
    };

    return (
        <Modal
            title=""
            visible={open}
            onOk={() => setOpen(false)}
            onCancel={() => setOpen(false)}
            footer={null}
        >
            <Form
                form={form}
                name="uploadPic"
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
            >
                <Row>
                    <Col
                        xs={24}
                    >
                       
                            <ImgCrop 
                                rotate
                                cropperProps={
                                    <Cropper
                                    image={fileList[fileList.length-1]}
                                    crop={crop}
                                    cropSize={{width:150,height:150}}
                                    zoom={zoom}
                                    aspect={4 / 3}
                                    onCropChange={setCrop}
                                    onCropComplete={onCropComplete}
                                    onZoomChange={setZoom}
                                />
                                }
                            >
                                <Upload
                                
                                    action="https://run.mocky.io/v3/14d541cf-69d3-49b1-a98c-e374ecf7e243"
                                    listType="picture-card"
                                    fileList={fileList}
                                    onChange={onChange}
                                    // onPreview={onPreview}
                                    // previewFile={null}
                                    maxCount={1}
                                >
                                    {fileList.length < 5 && '+ Upload'}
                                </Upload> 

                            </ImgCrop>
                            {/* src="https://image-data-s3.s3.ap-southeast-1.amazonaws.com/362d21eeb0ec2bb48d12d82609cc7458" */}
                            {/* <img src={showImage} width="100" height="100"  /> */}
                    </Col>

                    

                    <Col xs={24}>
                        <Button
                            htmlType="submit"
                            type="primary"
                            style={{ width: "100%" }}
                            loading={loadingCreate}
                        >
                            
                        </Button>
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
